@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,700;1,900&display=swap);
footer{width:100%;height:-webkit-max-content;height:max-content;background-color:#000000;color:white}footer .footer-container{width:100%;display:flex;flex-flow:row nowrap;justify-content:space-between;margin:0 auto;padding:3em 4em}@media (max-width: 700px){footer .footer-container{flex-flow:column nowrap}}footer .footer-container section{flex-grow:1}@media (max-width: 400px){footer .footer-container section{border-bottom:1px solid grey;padding:1em 0}footer .footer-container section h3{display:flex;justify-content:space-between}}footer .footer-container section h3{font-size:1.3em}footer .footer-container section h3 span{display:none}@media (max-width: 400px){footer .footer-container section h3 span{display:inline-block;font-size:1.5em;margin-left:2em}}footer .footer-container section a{color:grey;text-decoration:none;display:block;padding:0.4em 0.2em;transition:ease all 0.3s}footer .footer-container section a:hover{color:#d3d3d3}@media (max-width: 400px){footer .footer-container section a{display:none}}footer .footer-container section svg{width:2.5em;margin:0.2em;cursor:pointer}footer p{border-top:1px solid grey;padding:10px;margin:0;text-align:center}

.NavMenu{display:flex;flex-flow:row wrap;justify-content:center;align-items:center;height:80px;width:100%;background-color:#000000;color:#FFFFFF}@media (max-width: 600px){.NavMenu{display:none;width:90%;z-index:10;height:70vh;background-color:#FFF;box-shadow:0 3px 6px #00000029;position:absolute;top:0;flex-flow:column nowrap;justify-content:flex-start;align-items:flex-end;transition:ease-in-out all 0.3s}}.NavMenu .close-icon{display:none}@media (max-width: 600px){.NavMenu .close-icon{display:block;width:2em;margin-right:2em;cursor:pointer}.NavMenu .close-icon .a{fill:#5B17AA;stroke:#5B17AA;stroke-width:0px}}.NavMenu ul{display:flex;justify-content:center;align-items:center;flex-flow:row nowrap;padding:0.5em 10em;margin:0;width:100%;z-index:10}@media (max-width: 600px){.NavMenu ul{flex-flow:column nowrap;padding:0;align-items:flex-start}}.NavMenu ul li:nth-child(6) a{border-right:none}@media (max-width: 600px){.NavMenu ul li{margin:10px 5px}}.NavMenu ul a{color:#FFF;font-family:helvetica;font-weight:bold;font-size:1.5em;text-decoration:none;padding:0.3em 1em;border-right:1px solid;transition:ease all 0.5s}.NavMenu ul a:hover{color:#a5a3a3}@media (max-width: 600px){.NavMenu ul a{border:transparent;color:#000000;margin:1em 0}}

.Navbrand{width:100%;height:80px;background-color:white;display:flex;flex-flow:row nowrap;padding:0.8em 1em;align-items:flex-end}@media (min-width: 600px){.Navbrand .burguer-menu{display:none}}@media (max-width: 700px){.Navbrand{flex-flow:row wrap;height:-webkit-max-content;height:max-content;align-items:center}.Navbrand .shopping-card{order:2}}.Navbrand .shopping-card{padding:0.5em 1.3em;cursor:pointer;position:relative}.Navbrand form{flex-grow:1}@media (max-width: 500px){.Navbrand form{width:100%;order:3}.Navbrand form .search-box{width:100%}}.Navbrand p{font-size:3em;color:black;text-transform:uppercase;font-family:'Bebas Neue', cursive;margin:0;text-align:left;flex-grow:1;height:100%;display:flex;justify-content:space-around}.Navbrand .search-box{display:flex;justify-content:right;height:45px;width:80%}.Navbrand .search-box input{border:1px solid transparent;border-radius:2px;background-color:#EEEEEE;color:#6B6B6B;padding:0.5em 1.3em;width:25rem;height:45px}.Navbrand .search-box input:focus{outline:transparent}.Navbrand .search-box button{border:1px solid transparent;background-color:#DEDEDE;height:45px}.Navbrand .card-box{flex-grow:1;display:flex;flex-flow:row nowrap;justify-content:space-between;padding:0.5em 1.3em}@media (max-width: 600px){.Navbrand .card-box{display:none}}.Navbrand .card-box svg{cursor:pointer;width:2em}.Navbrand .card-box button{border:solid 1px black;background-color:white}

.card-producto-container{width:15em;border:1px solid #E6E6E6;margin:0 auto}.card-producto-container img{width:100%;height:15em}.card-producto-container div{width:100%;display:flex;flex-flow:column nowrap;background-color:#F4F4F4;padding:1em 2em}.card-producto-container div p{font-weight:500}.card-producto-container div .price{font-family:Helvetica,sans-serif;color:#5B17AA;font-weight:bold}.card-producto-container div button{display:block;width:100%;height:-webkit-max-content;height:max-content;border:1px solid black;background-color:black;color:white;padding:0.4em;font-size:0.75em}.card-producto-container div button svg{fill:white;width:1em;margin-right:0.5em;height:1em}

.MainCards{display:flex;flex-flow:row nowrap;justify-content:space-around;width:100%;font-size:5em}@media (max-width: 600px){.MainCards{flex-flow:column;width:90%;margin:0 auto}}.MainCards .modaInfantil{background-image:url(/github.io/clothe-store/static/media/categorias-destacadas-moda-infantil.79a62317.png);background-repeat:no-repeat;background-size:cover}.MainCards .proteccion{background-image:url(/github.io/clothe-store/static/media/categorias-destacadas-proteccion.829da140.png);background-repeat:no-repeat;background-size:cover}.MainCards .proteccion,.MainCards .modaInfantil{display:flex;justify-content:center;align-items:center;font-family:'Roboto', sans-serif;text-shadow:4px 2px 5px #000000B8;color:#FFFFFF;width:100%;height:20em;margin:1em 0}.MainCards .proteccion h3,.MainCards .modaInfantil h3{font-size:2.5em;font-weight:bold;font-style:italic;margin-top:3.5em}

*::after,*::before{box-sizing:inherit}*{font-size:16px;font-family:Helvetica,sans-serif}@media (max-width: 900px){*{font-size:14px}* svg{width:2em;vertical-align:top}}html,body{box-sizing:border-box}ul,li{list-style:none;padding:0;margin:0}svg{cursor:pointer}

